.recipe
{
    border-radius: 20px;
    box-shadow: 0px 5px 20px grey;
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
}

.image
{
    border-radius: 10px;
    width: 100px;
    height: 100px;
}